.About_maindiv__3qyjG {
  background-color: transparent;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}
.About_leftdiv__2N6QS {
  background-color: transparent;
  height: 100vh;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  left: 3vw;
}
.About_rightdiv__2Mhsl {
  background-color: transparent;
  /* min-height: 100vh; */
  height: 90vh;
  width: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  /* justify-content: center; */
  padding-right:  6vw;
  overflow-y: scroll;

  /* background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px); */
}

.About_title__1YyuC {
    font-family: Dark;
    color: white;
    font-size: 4vw;
    font-weight: 400;
    /* line-height: 150px; */
    height: 100%;
    background: linear-gradient(
      90.21deg,
      #6fffff 0.13%,
      #ff8eff 48.16%,
      #b54eff 99.82%
    );

    background-size: 200% 200%;
	  -webkit-animation: About_gradient__1zjmh 3s ease infinite;
	          animation: About_gradient__1zjmh 3s ease infinite;

    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  
    margin-top: 4%;
    height: auto;
    margin-bottom: 4%;
}

@-webkit-keyframes About_gradient__1zjmh {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes About_gradient__1zjmh {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.About_textcontent__3siBm{
    font-family: 'Poppins', sans-serif;
    line-height: 28px;
    /* line-height: 1.7vw; */
    font-weight: 500;
    font-size: 1.1vw;
    color: white;
    /* padding-left:3vw;
    padding-right:5.5vw; */

}

.About_glow__23xOB {
  width: 100%;
  height: 100%;
  -webkit-animation: About_scale__W-iZo 5s infinite;
          animation: About_scale__W-iZo 5s infinite;
}

.About_card__2p6qx {
  width: 42%;
  /* height: 300px; */
  z-index: 5;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  -webkit-animation: About_floating__3ymxw 3.5s infinite ease-in-out;
          animation: About_floating__3ymxw 3.5s infinite ease-in-out;
  /* cardscale 7s infinite ease-in-out */
}

@-webkit-keyframes About_floating__3ymxw {
    0% { -webkit-transform: translate(0,  0px) scale(1); transform: translate(0,  0px) scale(1);
    }
    50%  { -webkit-transform: translate(0, 15px) scale(0.97); transform: translate(0, 15px) scale(0.97); }
    100%   { -webkit-transform: translate(0, -0px) scale(1); transform: translate(0, -0px) scale(1);}   
}

@keyframes About_floating__3ymxw {
    0% { -webkit-transform: translate(0,  0px) scale(1); transform: translate(0,  0px) scale(1);
    }
    50%  { -webkit-transform: translate(0, 15px) scale(0.97); transform: translate(0, 15px) scale(0.97); }
    100%   { -webkit-transform: translate(0, -0px) scale(1); transform: translate(0, -0px) scale(1);}   
}

@-webkit-keyframes About_scale__W-iZo {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    50% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
}

@keyframes About_scale__W-iZo {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    50% {
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
}

@-webkit-keyframes About_cardscale__1jxL4 {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    50% {
      -webkit-transform: scale(0.95);
              transform: scale(0.95);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
}

@keyframes About_cardscale__1jxL4 {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    50% {
      -webkit-transform: scale(0.95);
              transform: scale(0.95);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
}

